* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
  'Open Sans', 'Helvetica Neue', sans-serif;
  outline: none;
  color: black;
}

.wrapper {
  max-width: 40%;
  margin: 100px auto;
  min-width: 720px;
}

.join-block {
  margin: 0 auto;
  width: 300px;
}

.join-block input {
  font-size: 24px;
  width: 100%;
  height: 50px;
  padding-bottom: 5px;
  padding-left: 10px;
  margin-bottom: 10px;
}

.join-block button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  font-weight: bold;
  cursor: pointer;
}

.chat {
  display: flex;
  height: 500px;
  border: 1px solid rgba(159, 183, 197, 0.2);
  border-radius: 5px;
  overflow: hidden;
}

.chat-users {
  border-right: 1px solid rgba(159, 183, 197, 0.1);
  padding: 20px;
  width: 200px;
  background-color: #f6f9fa;
}

.chat-messages {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  padding: 30px;
}

.messages {
  float: right;
  height: calc(100% - 100px);
  overflow: auto;
}

.message {
  margin-bottom: 10px;
}

.message-body {
  font-size: 12px;
  color: #fff;
  box-sizing: border-box;
  padding: 0.7rem 1.2rem;
  background: #FFF;
  border-radius: 1.125rem 1.125rem 1.125rem 0;
  min-height: 2.25rem;
  width: fit-content;
  max-width: 80%;
  box-shadow:
          0 0 2rem rgba(black, 0.075),
          0rem 1rem 1rem -1rem rgba(black, 0.1);
}

.message span {
  opacity: 0.5;
  font-size: 11px;
}

.chat-users ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.chat-users ul li {
  margin-top: 10px;
  border-radius: 3px;
  background-color: #fff;
  padding: 8px 14px;
  box-shadow: 0 3px 5px rgba(159, 183, 197, 0.1);
  font-weight: 500;
}

.chat-messages form {
  /*margin-top: 20px;*/
  padding-top: 20px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.chat-messages form textarea {
  width: 100%;
  margin-bottom: 10px;
  resize: none;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}